import List from "./List";
import Add from "./Add";
import Edit from "./Edit";

const key = "modules";

export default {
  key,
  routes: [
    {
      name: "Mdulos",
      path: `/admin/${key}`,
      component: List
    },
    {
      name: "Adicionar mdulo",
      path: `/admin/${key}/adicionar`,
      component: Add
    },
    {
      name: "Alterar mdulo",
      path: `/admin/${key}/:module`,
      component: Edit
    }
  ]
};
